* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, serif;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image-container {
  justify-content: center;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  padding-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars-container {
  display: flex;
}

.card__stars {
  justify-content: space-between;
  padding-top: 16px;
  display: flex;
}

.card__stars-star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.card__stars-star:last-of-type {
  background-image: url("star.822d561e.svg");
}

.card__stars-star:not(:last-of-type) {
  margin-right: 4px;
}

.card__stars-reviews {
  color: #060b35;
  align-self: flex-end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  padding-top: 24px;
  padding-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
}

.card__button-container {
  box-sizing: border-box;
  justify-content: center;
  height: 40px;
  display: flex;
}

.card__button {
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.card__button--styles {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  text-decoration: none;
}

.card__button--styles:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
